.aboutMe-section {
    padding: 40px;
    text-align: center;
    align-self: center;
    margin: 0 auto;
}

.aboutMe-section h1 {
    position: relative;
    margin-bottom: 20px; 
}

.aboutMe-section h1::after {
    content: ''; 
    position: absolute;
    width: 200px;
    height: 3px;
    border-radius: 5px;
    background-color: #823422;
    bottom: -10px; /* Adjust to place under the h1 */
    left: 50%; /* Center horizontally */
    transform: translateX(-50%); /* Center horizontally */
}
.about-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
}

.about-image img {
    max-width: 500px;
    width: 100%;
    border-radius: 10px;
}

.about-text {
    -webkit-border-radius: 25px;
    border-radius: 25px;
    -webkit-box-shadow: 29px 29px 67px #cba8a0, -29px -29px 67px #ffeee2;
    box-shadow: 29px 29px 67px #cba8a0, -29px -29px 67px #ffeee2;
    max-width: 600px;
    padding: 30px;
    border-radius: 10px;
    text-align: left;
}

.skill-1 {
    background-color: #fcfcdd;
    padding: 3px 5px;
    border-radius: 5px;
    margin: 0 2px;
}

.name{
    font-family: "Lobster", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 1.5rem;
}

.star:hover {
    display: inline-block;
    animation: sparkle 1.5s infinite ease-in-out;
    cursor: pointer;
}


/* Tablet View - max-width: 786px */
@media screen and (max-width: 786px) {
    .aboutMe-section {
        text-align: center;
    }
    .about-info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        /* text-align: center; */
        margin: 0 auto;
    }
    .about-text {
        width: 80%; 
        max-width: 600px; 
        margin: 20px auto; 
        padding: 20px;
        align-self: center;
    }
    .about-image img {
        width: 100%;
        max-width: 400px; 
        margin: 20px auto;
    }
}

/* Phone View - max-width: 480px */
@media screen and (max-width: 480px) {
    .aboutme {
        text-align: center;
    }
    .about-info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        /* text-align: center; */
        margin: 0 auto;
    }
    .about-text {
        width: 90%; 
        max-width: 350px; 
        margin: 0 auto; 
        padding: 15px; 
        align-self: center;
    }
    .about-image img {
        width: 100%;
        max-width: 300px; 
        margin: 20px auto; 
    }
}

@keyframes sparkle {
    0%, 100% {
      transform: scale(1);
      text-shadow: 0 0 5px #ffd700, 0 0 10px #ffd700, 0 0 20px #ffdd33, 0 0 30px #ffee88;
    }
    50% {
      transform: scale(1.3); /* Slightly enlarges the star */
      text-shadow: 0 0 8px #ffe066, 0 0 16px #ffe066, 0 0 24px #ffdd33, 0 0 40px #ffff99;
    }
}