.card {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: relative;
  margin: 3rem;
}

.card img {
  width: 300px;
  height: 200px;
  border-radius: 1rem;
  border: 1.5px solid black;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transition: transform 0.3s ease;
}

.card .text {
  flex: 1;
  text-align: left;
  margin-left: 300px;
  margin-top: -25 px;
  padding: 1rem;
  font-size: 1.1rem;
  z-index: 2;
  /* margin: 0 auto; */
}

.card .text p {
  margin-block-start: -10px;
}

.card .buttons {
  display: none;
  gap: 1rem;

}

.card .buttons button {
  width: 7rem;
  height: 2.5rem;
  border: 1px solid black;
  border-radius: 1.2rem;
  cursor: pointer;
  transition: background-color 0.3s;
}  
/* Individual Button Styling */
#code {
  background-color: black;
  color: aliceblue;
}

#live {
  background-color: rgb(99, 99, 249);
  color: aliceblue;
}

/* Hover Effects */
.card:hover img {
  transform: scale(1.2) translateX(20px); /* Move image slightly left and scale up */
  /* border: none; */
  border-radius: 0.3rem;
}

.card:hover .text {
  transform: scale(0.95);
  border-radius: 1rem;
  background-color: rgb(242, 177, 130);
}

.card .text h1{
  margin-bottom: 20px;
}

.card:hover .buttons {
  display: flex;
  justify-content: space-around;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .card {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1rem auto;
    position: static;
  }

  .card img {
  position: static;
  }
  .card .text {
    margin-left: 0;
    padding: 1rem;
    width: 100%;
  }

  .card .buttons {
    justify-content: center;
  }

  .card:hover img {
    transform: scale(1.2);
  }
}

/* Small mobile devices */
@media (max-width: 375px) {
  .card {
    padding: 0.5rem;
  }
  
  .card .text {
    font-size: 0.9rem;
    padding: 0.5rem 0;
    margin-left: 0;
  }
  
  .card .buttons button {
    width: 100%;
    margin: 0.25rem 0;
  }
}
