/* Skills.css */
.skill-section {
    padding: 4rem 1rem;
    z-index: -1;
    /* background-color: aqua; */
}
  
.skills {
    position: relative;
    padding: 2rem 1rem;
    text-align: start;
}
  
.skills h1 {
    margin-left: 20px;
    margin-bottom: 1rem;
    position: relative;
}
  
.skills h1::after {
    content: '';
    position: absolute;
    width: 0; 
    height: 3px;
    margin-top: 15px;
    margin-left: 10px;
    border-radius: 10px;
    background-color: chocolate;
    transition: width 1s ease-out; 
}
  
.skills.in-view h1::after {
    width: 50%;
}
  
.skills-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}  

@media (max-width: 768px) {
    .skills-list{
        align-items: center;
        align-self: center;
        margin: 2rem;
    }
}